@font-face {
  font-family: 'CenturyGothic';
  src: url('../fonts/CenturyGothic.woff2') format('woff2'), url('../fonts/CenturyGothic.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'CoreSans';
  src: url('../fonts/CoreSansE-35Light.woff2') format('woff2'), url('../fonts/CoreSansE-35Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Haettenschweiler';
  src: url('../fonts/Haettenschweiler.woff2') format('woff2'), url('../fonts/Haettenschweiler.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

body {
  font-size: 20px;
  font-family: 'CoreSans', sans-serif;
  background-color: #141212;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

abbr {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.accent-text {
  color: #5b8b2d;
}

.main-header {
  font-family: 'CenturyGothic', sans-serif;
  font-size: 16px;
  color: #92d5bc;
}

.main-header--wrap {
  box-sizing: border-box;
  padding: 5px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #050505 url(../img/header-bd.jpg) 0 0 no-repeat;
  background-size: cover;
}

.main-header--logo {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: 450px;
  height: auto;
  margin-top: auto;
  margin-bottom: auto;
}

.main-header--search {
  width: 450px;
  height: 50px;
  background-color: #fff;
  opacity: 0.2;
}

.contacts--item {
  margin-bottom: 6px;
}

.contacts--item:last-child {
  margin-bottom: 0px;
}

.contacts--link {
  margin-left: 25px;
  position: relative;
  color: #92d5bc;
}

.contacts--link::before {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.contacts--link__e-mail::before {
  width: 21px;
  height: 15px;
  left: -28px;
  background: url(../../img/mail.png) center no-repeat;
  background-size: cover;
}

.contacts--link__phone::before {
  width: 18px;
  height: 19px;
  left: -25px;
  background: url(../../img/phone.png) center no-repeat;
  background-size: cover;
}

.contacts--link__location::before {
  width: 16px;
  height: 22px;
  left: -24px;
  background: url(../../img/location.png) center no-repeat;
  background-size: cover;
}

.discounts {
  position: relative;
  margin-bottom: 10px;
}

.discounts--wrap {
  position: relative;
}

.discounts--item {
  margin: 0 auto;
  display: none;
  position: relative;
  overflow: hidden;
}

.discounts--item__active {
  display: block;
}

.discounts--header {
  font-size: 50px;
  font-weight: 700;
  color: #232e30;
  margin-top: 100px;
  margin-left: 100px;
}

.discounts--img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.discounts--info {
  margin-top: 30px;
  margin-bottom: 50px;
  padding: 10px 100px;
  color: #95b1b5;
  background-color: rgba(0, 0, 0, 0.5);
}

.discounts--btns {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.discounts--btn {
  font-size: 0;
  border: none;
  padding: 0;
  width: 27px;
  height: 52px;
  background: url(../img/arrow.png) center no-repeat;
}

.discounts--btn__prev {
  transform: rotate(180deg);
}

.discounts--checks {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  justify-content: space-between;
  width: 70px;
  margin-bottom: 10px;
}

.discounts--check {
  font-size: 0;
  border: none;
  padding: 0;
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: 2px solid #8BBEAD;
  border-radius: 50%;
}

.services--list {
  margin-left: -5px;
  margin-right: -5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.services--item {
  position: relative;
  width: calc(50% - 10px);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 10px;
  background-color: #99ce94;
}

.services--item__long {
  width: 100%;
}

.services--item__long .service--item {
  width: calc(16.666666666666668% - 10px);
}

.services--header {
  font-size: 27px;
  color: #394d44;
  background-color: #99ce94;
}

.services--header__left {
  margin-right: 20px;
  padding-right: 5px;
  text-align: right;
}

.services--header__right {
  margin-left: 20px;
  padding-left: 5px;
  text-align: left;
}

.services--header__long {
  text-align: center;
}

.services--insure {
  color: #192822;
}

.services--bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 100;
  opacity: 0.5;
}

.services--bg__left {
  background-image: url(../img/services-left-bg.png);
}

.services--bg__right {
  background-image: url(../img/services-right-bg.png);
}

.services--bg__long {
  background-image: url(../img/services-long-bg.png);
}

.services--wrap {
  padding-top: 35px;
  padding-bottom: 85px;
  background-color: #588b52;
}

.service--list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.service--item {
  width: 30%;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.service--item:first-child {
  margin-left: 20px;
}

.service--item:last-child {
  margin-right: 20px;
}

.service--header {
  margin-top: -65px;
  margin-bottom: 5px;
  padding-bottom: 30px;
  font-family: 'Haettenschweiler', sans-serif;
  font-size: 32px;
  color: #192822;
  background-color: #588b52;
}

.service--about {
  flex-grow: 1;
  margin-bottom: 15px;
  font-size: 18px;
  color: #394d44;
}

.service--price {
  margin-bottom: 5px;
  font-size: 14px;
  color: #192822;
  font-weight: bold;
}

.service--link {
  margin-bottom: 10px;
}

.about-us--wrap {
  padding: 1px;
  padding-bottom: 200px;
  border-bottom-right-radius: 75px;
  border-bottom-left-radius: 75px;
  background: url(../img/about-us-bg.jpg) center no-repeat;
  background-size: cover;
}

.about-us--header {
  margin-left: 47%;
  margin-bottom: 35px;
  margin-top: 70px;
  font-size: 48px;
  font-family: 'CenturyGothic', sans-serif;
  color: #161e1b;
}

.about-us--info {
  margin-left: 47%;
  margin-bottom: 100px;
  padding-right: 70px;
  font-size: 23px;
  font-family: 'CenturyGothic', sans-serif;
}

.faq {
  margin-top: -170px;
  margin-bottom: 100px;
  font-family: 'CenturyGothic', sans-serif;
  font-size: 46px;
  color: #161e1b;
}

.faq--wrap {
  padding: 20px 0;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.42);
}

.faq--header {
  display: inline-block;
}

.faq--link {
  color: #446822;
}

.faq--text {
  display: inline-block;
}

.main-footer {
  padding-bottom: 30px;
  font-family: 'CenturyGothic', sans-serif;
  font-size: 18px;
  color: #35521a;
}

.main-footer--wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 30px;
}

.main-footer--item {
  margin-bottom: 8px;
}

.main-footer--link {
  color: #35521a;
  border-bottom: 1px solid #35521a;
}

.main-footer--asks {
  order: 2;
  font-size: 36px;
}

.main-footer--phone {
  display: block;
  margin-bottom: 20px;
  padding-right: 45px;
  font-size: 36px;
  color: #35521a;
  text-align: right;
}

.main-footer--address {
  text-align: right;
}

@media screen and (max-width: 1400px) {
  .main-header {
    font-size: 14px;
  }

  .main-header--logo {
    width: 350px;
  }

  .main-header--search {
    width: 300px;
  }

  .contacts--link__e-mail::before {
    width: 18px;
    height: 13px;
    left: -25px;
  }

  .contacts--link__phone::before {
    width: 15px;
    height: 16px;
    left: -23px;
  }

  .contacts--link__location::before {
    width: 13px;
    height: 18px;
    left: -21px;
  }

  .discounts--header {
    margin-top: 25px;
    margin-left: 75px;
  }

  .discounts--info {
    padding: 5px 75px;
    font-size: 19px;
    line-height: 1.45;
  }

  .services--header {
    font-size: 24px;
    text-align: center;
    margin-right: 0;
    margin-left: 0;
    padding: 3px 0;
  }

  .services--wrap {
    padding-top: 25px;
    padding-bottom: 60px;
  }

  .service--item:first-child {
    margin-left: 0;
  }

  .service--item:last-child {
    margin-right: 0;
  }

  .service--header {
    margin-top: -45px;
    padding-bottom: 15px;
    font-size: 24px;
  }

  .service--about {
    font-size: 17px;
  }

  .service--link {
    font-size: 17px;
  }

  .about-us--wrap {
    padding-bottom: 140px;
  }

  .about-us--header {
    margin-top: 50px;
    margin-bottom: 25px;
    margin-left: 40%;
    font-size: 40px;
  }

  .about-us--info {
    margin-bottom: 80px;
    margin-left: 40%;
    padding-right: 35px;
    font-size: 20px;
  }

  .faq {
    margin-bottom: 120px;
    font-size: 31px;
  }

  .main-footer {
    font-size: 16px;
  }

  .main-footer--asks {
    font-size: 26px;
  }

  .main-footer--phone {
    font-size: 26px;
  }
}

@media screen and (max-width: 980px) {
  .container {
    padding-left: 5px;
    padding-right: 5px;
  }

  .main-header--search {
    width: 100px;
  }

  .contacts--item {
    margin-bottom: 2px;
  }

  .contacts--item:last-child {
    margin-bottom: 2px;
  }

  .discounts {
    margin-bottom: 5px;
  }

  .discounts--header {
    margin-left: 50px;
  }

  .discounts--info {
    padding: 5px 50px;
    font-size: 17px;
    line-height: 1.4;
    background-color: rgba(0, 0, 0, 0.6);
  }

  .md-hidden {
    display: none;
  }

  .services--item {
    width: 100%;
    margin-bottom: 7px;
  }

  .services--item__long .service--item {
    width: calc(25% - 10px);
  }

  .service--item {
    width: calc(25% - 10px);
  }

  .service--header {
    font-size: 28px;
  }

  .about-us--header {
    margin-left: 35%;
  }

  .about-us--info {
    margin-left: 35%;
    padding-right: 10px;
    font-size: 18px;
  }

  .faq {
    font-size: 34px;
    margin-bottom: 90px;
  }

  .faq--wrap {
    padding: 10px 0;
  }

  .main-footer {
    font-size: 14px;
  }

  .main-footer--asks {
    font-size: 20px;
  }

  .main-footer--phone {
    font-size: 20px;
  }
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 0px;
    padding-right: 0px;
  }

  .main-header--wrap {
    flex-wrap: wrap;
  }

  .main-header--logo {
    width: calc(66.66666666666667% - 10px);
  }

  .main-header--logo {
    margin: 0 auto;
    margin-bottom: 20px;
  }

  .main-header--search {
    width: calc(83.33333333333334% - 10px);
  }

  .main-header--search {
    width: 200px;
    margin-left: auto;
    order: 1;
    display: none;
  }

  .contacts--list {
    width: calc(83.33333333333334% - 10px);
  }

  .contacts--list {
    display: flex;
    justify-content: space-around;
  }

  .contacts--item {
    margin-bottom: 10px;
  }

  .contacts--item:last-child {
    margin-bottom: 10px;
  }

  .sm-hidden {
    display: none;
  }

  .discounts--header {
    margin-left: 50px;
  }

  .services--list {
    margin-left: 0;
    margin-right: 0;
  }

  .services--item {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 5px;
  }

  .service--header {
    font-size: 22px;
  }

  .about-us--wrap {
    padding-bottom: 120px;
  }

  .about-us--header {
    margin-top: 20px;
    margin-left: 0;
    text-align: center;
  }

  .about-us--info {
    margin-left: 0;
    padding-right: 0;
    text-align: center;
  }

  .faq {
    font-size: 29px;
    margin-bottom: 110px;
  }

  .main-footer {
    font-size: 16px;
    padding-bottom: 0;
    text-align: center;
  }

  .main-footer--wrap {
    padding: 0 20px;
    justify-content: center;
  }

  .main-footer--wrapper {
    width: 100%;
    order: -1;
  }

  .main-footer--nav {
    width: 100%;
  }

  .main-footer--phone {
    margin-bottom: 10px;
    padding-right: 0;
    text-align: center;
  }

  .main-footer--address {
    margin-bottom: 30px;
    text-align: center;
  }
}

@media screen and (max-width: 560px) {
  .main-header {
    font-size: 12px;
  }

  .main-header--wrap {
    display: block;
    padding: 5px 0;
  }

  .main-header--logo {
    width: calc(75% - 10px);
  }

  .main-header--logo {
    margin-right: auto;
    margin-left: 5px;
  }

  .contacts--list {
    width: calc(100% - 10px);
  }

  .contacts--link__e-mail {
    margin-left: 22px;
  }

  .contacts--link__e-mail::before {
    left: -21px;
  }

  .contacts--link__phone {
    margin-left: 15px;
  }

  .contacts--link__phone::before {
    width: 13px;
    height: 14px;
    left: -13px;
  }

  .contacts--link__location {
    margin-left: 17px;
  }

  .contacts--link__location::before {
    left: -16px;
  }

  .xs-hidden {
    display: none;
  }

  .discounts--header {
    margin-top: 10px;
    font-size: 35px;
  }

  .discounts--img {
    min-width: 100%;
    height: 150%;
  }

  .discounts--info {
    font-size: 15px;
    padding: 5px 40px;
    line-height: 1.4;
  }

  .xs-hidden {
    display: none;
  }

  .services--item__long .service--item {
    width: calc(50% - 10px);
  }

  .services--header {
    font-size: 20px;
  }

  .service--item {
    width: calc(50% - 10px);
  }

  .service--header {
    font-size: 25px;
  }

  .about-us--wrap {
    border-bottom-right-radius: 50px;
    border-bottom-left-radius: 50px;
  }

  .about-us--info {
    padding: 0 10px;
    font-size: 16px;
  }

  .faq {
    font-size: 22px;
  }
}

@media screen and (max-width: 450px) {
  .discounts--img {
    min-width: 100%;
    height: 100%;
  }
}

@media screen and (max-width: 350px) {
  .main-header {
    font-size: 10px;
  }

  .discounts--img {
    min-width: 150%;
    height: 100%;
  }
}