.contacts {
	&--list {
		.size-sm(10);
    .size-xs(12);
    .sm-block({
      display: flex;
      justify-content: space-around;
      });
  }

  &--item {
    margin-bottom: 6px;

    .md-block({
     margin-bottom: 2px;
     });
    .sm-block({
      margin-bottom: 10px;
      });

    &:last-child {
     margin-bottom: 0px;
     .md-block({
       margin-bottom: 2px;
       });
     .sm-block({
       margin-bottom: 10px;
       });
   }
 }

 &--link {
  margin-left: 25px;
  position: relative;

  color: @header-color;

  &::before {
   content: '';
   position: absolute;
   top: 50%;
   transform: translateY(-50%);
 }
 &__e-mail {
  .xs-block({
    margin-left: 22px;
    });
  &::before {
    width: 21px;
    height: 15px;
    left: -28px;

    background: url(../../img/mail.png) center no-repeat;
    background-size: cover;

    .lg-block({
     width: 18px;
     height: 13px;
     left: -25px;
     });
    .xs-block({
      left: -21px;
      });
  }
}

&__phone {
  .xs-block({
    margin-left: 15px;
    });

  &::before {
    width: 18px;
    height: 19px;
    left: -25px;

    background: url(../../img/phone.png) center no-repeat;
    background-size: cover;

    .lg-block({
     width: 15px;
     height: 16px;
     left: -23px;
     });
    .xs-block({
      width: 13px;
      height: 14px;
      left: -13px;
      });
  }
}

&__location {
  .xs-block({
    margin-left: 17px;
    });
  &::before {
    width: 16px;
    height: 22px;
    left: -24px;

    background: url(../../img/location.png) center no-repeat;
    background-size: cover;

    .lg-block({
     width: 13px;
     height: 18px;
     left: -21px;
     });
    .xs-block({
      left: -16px;
      });
  }
}
}
}

